button.co-btn,
a.co-btn {
		position: relative;
		width: 100%;
		font-size: 0;
		font-family: "NotoSansKR";
		padding: 0.75rem;
		border-radius: 1.75rem;
		text-align: center;
		color: #fff;
		background-color: #253981;
		display: inline-block;
		vertical-align: top;
}
button.co-btn > span,
a.co-btn > span {
		position: relative;
		font-size: 1rem;
		font-weight: 400;
		line-height: 1.5rem;
		font-family: "NotoSansKR";
}
button.co-btn > span.alt-txt,
a.co-btn > span.alt-txt {
		position: absolute;
		left: 0;
		top: 0;
		width: 1px;
		height: 1px;
		font-size: 1px;
		line-height: 1px;
		opacity: 0;
		color: transparent;
		display: block;
}
button.co-btn ~ .co-btn,
a.co-btn ~ .co-btn {
		margin-left: 1.25rem;
}
button.co-btn:disabled,
a.co-btn:disabled {
		background-color: #b9c0d9;
}
button.is-disable,
a.is-disable {
		background-color: #b9c0d9;
}
button.is-disable:disabled,
a.is-disable:disabled {
		cursor: not-allowed;
}
button.is-outline-navy-1,
a.is-outline-navy-1 {
		border-width: 1px;
		border-style: solid;
		background-color: #fff;
