.skip {
		position: absolute;
		overflow: hidden;
		clip: rect(0 0 0 0);
		margin: -1px;
		padding: 0;
		width: 1px;
		height: 1px;
		border: 0;
		font-size: 1px;
		line-height: 1px;
		color: transparent;
}

.alt-txt {
		position: absolute !important;
		left: 0 !important;
		top: 0 !important;
		width: 1px !important;
		height: 1px !important;
		font-size: 1px !important;
		line-height: 1px !important;
		opacity: 0 !important;
		color: transparent !important;
		display: block !important;
}
.alt-txt.type-title {
		position: relative !important;
		width: 100% !important;
		left: auto !important;
		top: auto !important;
}

.btn-move-top {
		position: fixed;
		right: 1.25rem;
		bottom: 1.25rem;
		width: 2.75rem;
		height: 2.75rem;
		border-radius: 0.75rem;
		opacity: 0.9;
		background-color: #253981;
		background-image: url("../images/ico/ico_arrow_top_white_01.svg");
		background-position: center center;
		background-size: 2.125rem 2.125rem;
		background-repeat: no-repeat;
		z-index: 100;
		transition: bottom 0.3s ease-in-out;
}
.btn-move-top.hide {
		display: none;
}

.kmi-scroll,
.textarea-scroll {
		position: absolute;
		right: 0.25rem;
		top: 0.75rem;
		width: 0.25rem;
		height: calc(100% - 1.5rem);
		background-color: transparent;
		opacity: 0;
		overflow: hidden;
		z-index: 2500;
		transition: opacity 0.1s ease-in-out;
}
.kmi-scroll span,
.textarea-scroll span {
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		width: 0.25rem;
		height: 2.5rem;
		background-color: rgba(28, 28, 28, 0.1);
		border-radius: 1000px;
}
.kmi-scroll.show,
.textarea-scroll.show {
		opacity: 1;
}

.c-layer-popup .modal-wrap .modal-field .kmi-scroll {
		z-index: 1997;
}

.kmi-popup.show.type-full .kmi-scroll, .kmi-popup.show.type-full-footer .kmi-scroll {
		position: fixed;
		top: 4.75rem;
		height: calc(100% - 5.5rem);
}
.kmi-popup.show .kmi-scroll {
		z-index: 2501;
}

.c-txtarea-field {
		position: relative;
}
.c-txtarea-field .textarea-scroll {
		height: calc(100% - 2.5rem);
		top: 1.25rem;
}

