}
.swiper.examResSwiper .swiper-wrapper .swiper-slide {
		width: 260px;
}
.swiper.examResSwiper .swiper-wrapper + .swiper-pagination {
		display: none;
}
.swiper.examResSwiper.swiper-ready .swiper-wrapper + .swiper-pagination {
		display: flex;
}

.swiper-pagination {
		position: relative;
		bottom: auto;
		left: auto;
		height: 0.5rem;
		margin-top: 0.75rem;
		display: flex;
		align-items: center;
		justify-content: center;
}
.swiper-pagination .swiper-pagination-bullet {
		min-width: 10px;
		width: 10px;
		height: 10px;
		border: 0 none;
		border-radius: 0.5rem;
		background: rgba(28, 28, 28, 0.1);
		opacity: 1;
		margin: 0 6px;
		transition: background-color 0.3s ease-in-out, width 0.3s ease-in-out;
}
.swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
		min-width: 16px;
		background: #253981;
		border-radius: 1.25rem;
}

.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
		bottom: 0;
}

.swiper-container-horizontal > .swiper-pagination-bullets > .swiper-pagination-bullet {
		margin: 0 6px;
}

.btn-swiper-stop {
		position: absolute;
		left: 0;
		top: 0;
		width: 1px;
		height: 1px;
		background-color: transparent;
		z-index: 2;
}

#container .btn-bottom-floating {
		position: sticky;
		z-index: 100;
		left: 0;
		bottom: 0;
		padding: 0.75rem 1.75rem;
		margin-left: -1.75rem;
		width: calc(100% + 3.5rem);
		background-color: #fff;
}
#container .btn-bottom-floating::after {
		content: "";
		display: block;
		position: absolute;
		left: 0;
		top: -2.25rem;
		width: 100%;
		height: 0;
		opacity: 0;
		background-image: linear-gradient(to top, rgba(25, 25, 25, 0.05) 0%, rgba(217, 217, 217, 0) 38.05%);
		transition: opacity 0.3s ease-in-out;
}
#container .btn-bottom-floating a,
#container .btn-bottom-floating button {
		margin: 0;
}
#container.isFloatingScroll .btn-move-top {
		bottom: 5.75rem;
}
#container.isFloatingScroll .btn-bottom-floating::after {
		height: 2.25rem;
		opacity: 1;
}
#container .c-btn-bottom.type01 {
		margin-top: 3.75rem;
}

